<template>
	<div class="customBox">
		<yd-navbar slot="navbar" title="考勤签到">
			<a @click="goBack" slot="left">
				<yd-navbar-back-icon></yd-navbar-back-icon>
			</a>
			<router-link to="signHistory" slot="right">
				<yd-icon name="schedule" color="#3399ff"></yd-icon>
			</router-link>
		</yd-navbar>
		<section>
			<div class="timeBox">
				<div class="timeContent">
					<p>
						<time>
							<span>{{time}}</span>
							<span>{{Seconds}}</span>
						</time>
						<span v-html="get_day()"></span>
						
					</p>
					
				</div>
			</div>
			
			<div class="address">
				<span>
					<h1>当前定位：</h1>
					<h2>{{addressInfo.address}}</h2>
				</span>
				<span>
					<h1>距离公司：</h1>
					<h2>{{addressInfo.distance}}米</h2>
				</span>
			</div>
			
			
			<div class="SignBox">
				<div></div>
				<ul>
					
					<li>
						<dl>
							<dd class="SignTitle">
								<span>
									<h1></h1>
									<h2></h2>
								</span>
								<span>打卡时间 {{signInData.createTime}}</span>
								<span>上班时间09:00</span>
							</dd>
							<dd v-if="signInData.createTime!=undefined">
								
								<div class="place">
									<i class="yd-icon-check-work1"></i>
										<span>打卡位置:</span>
									<span>{{signInData.addresses}}</span>
								</div>
								<div class="state">
									<span>
										<h4>打卡状态:</h4>
										<span>{{getSignStatus(signInData.regFlag)}}</span>
									</span>
									<p>
										<span>备注说明:</span>
										<router-link :to="{name:'oaSignInRemark'}">
											<div class="remarks" v-if="$route.params.signRemark">
												<p>{{$route.params.signRemark}}</p>
											</div>
											<h2 v-else>添加备注</h2>
										</router-link>
									</p>
								</div>
							</dd>
							<dd v-else class="SignIn">
								<span @click="signIn">上班打卡</span>
							</dd>
						</dl>
					</li>
					<li>
						<dl>
							<dd class="SignTitle">
								<span>
									<h1></h1>
									<h2  class="colorAsh"></h2>
								</span>
								<span>打卡时间{{signInData.createTime}}</span>
								<span>下班时间18:00</span>
							</dd>
							<dd v-if="signOutData.createTime!=undefined">
								
								<div class="place">
									<i class="yd-icon-check-work1"></i>
										<span>打卡位置:</span>
									<span>{{signInData.addresses}}</span>
								</div>
								<div class="state">
									<span>
										<h4>打卡状态:</h4>
										<span>{{getSignStatus(signInData.regFlag)}}</span>
									</span>
									<p>
										<span>备注说明:</span>
										<router-link :to="{name:'oaSignInRemark'}">
											<div class="remarks" v-if="$route.params.signRemark">
												<p>{{$route.params.signRemark}}</p>
											</div>
											<h2 v-else>添加备注</h2>
										</router-link>
									</p>
								</div>
							</dd>
							<dd v-else class="SignIn">
								<span @click="signOut">下班打卡</span>
							</dd>
						</dl>
					</li>
				</ul>
			</div>			
		</section>
		
	</div>

</template>
<script type="text/ecmascript-6">
	import Vue from 'vue';
	import { Layout } from 'vue-ydui/dist/lib.rem/layout';
	import MapView from '@/components/common/Map.vue';
	import router from '@/router/index';
	import { Icons } from 'vue-ydui/dist/lib.rem/icons';
	import { Flow, FlowState, FlowLine } from 'vux';
	import {MP} from '../../../static/js/map';
	import wx from 'weixin-js-sdk';
	
	Vue.component(Icons.name, Icons);
	Vue.component(Flow.name, Flow);
	Vue.component(FlowState.name, FlowState);
	Vue.component(FlowLine.name, FlowLine);
	export default {　　
		data() {　　　　
			return {　　　　　　
				height: 7,
				addressInfo:{
					longitude: 0.0,
					latitude: 0.0,
					address:"定位中..",
					distance:0,
					signRemark:""
				},			
				isShowIn: "",
				isShowOut: "",
				showLocateBtn: 'none',
				sysVer:"",
				changedPoint:{},
				time: '00:00',
				Seconds:'00',
				comPoint:{//对比坐标
					lat:113.369199,
					lng:23.096371
				},
				signInData:{},
				signOutData:{},
				userInfo:{
					type:''
				}
				
			}　　
		},
		components: {
		    Flow,
		    FlowState,
		    FlowLine
		},
		computed: {
			isShowInBtn: function() {
				return this.isShowIn == true ? true : false;
			},
			isShowOutBtn: function() {
				return this.isShowOut == true ? '' : 'none';
			},
			isShowBtn: function() {
				if(this.isShowOut == true || this.isShowIn == true) {
					return 'none'
				}
				return '';
			}
			
		},
		components: {　　　　
			MapView,
			
		},
		methods: {			
			//签到
			signIn() {
				console.info("签到数据："+JSON.stringify(this.addressInfo));
				var url = _baseUrl + "/mobile/oa/signIn.do";				
				this.$ajax({
						url: url,
						method: 'post',
						params: {
							addressInfo: this.addressInfo
						}
					}).then(response => {
						var rtn = response.data;
						if(rtn.success) {
							this.$dialog.toast({
								mes: rtn.message,
								timeout: 800,
								icon: 'success',
							});
						}

					})
					.catch(error => {
						console.log(error);
				});
			},
			//签退
			signOut() {	
				var url = _baseUrl + "/mobile/oa/signOut.do";
				this.$ajax({
						url: url,
						method: 'post',
						params: {
							addressInfo: this.addressInfo
						}
					}).then(response => {
						var rtn = response.data;
						if(rtn.success) {
							this.$dialog.toast({
								mes: rtn.message,
								timeout: 800,
								icon: 'success',
							});
						}
					})
					.catch(error => {
						console.log(error);
					});
			},
			//判断按钮是否可用
			isShowButton() {
				var url = _baseUrl + "/mobile/oa/isShowSignButton.do";
				this.$ajax.get(url).then(response => {
					var rtn = response.data;
					this.isShowOut = rtn.data.isShowOut;
					this.isShowIn = rtn.data.isShowIn;
					console.info("isShowIn:" + this.isShowIn + ",isShowOut:" + this.isShowOut);
				}).catch(function(error) {
					console.log(error);
				});
			},
			mySignHis() {
				router.push({
					name: 'signHistory'
				});

			},
			getTime(){
				let time,h,min;
				time = new Date();
				h = time.getHours();
				min = time.getMinutes();
				if( h<10 ) h = '0'+ h;
				if( min<10 ) min = '0' + min;
				this.time = h +':'+ min;
				
				setInterval(()=>{
					time = new Date();
					h = time.getHours();
					min = time.getMinutes();
					if( h<10 ) h = '0'+ h;
					if( min<10 ) min = '0' + min;
					this.time = h +':'+ min;
				},1000);
			},
			get_seconds(){
				let s;
				s = new Date().getSeconds();
				if( s<10 ) s = '0' + s;
				this.Seconds = s;
				
				setInterval(()=>{
					s = new Date().getSeconds();
					if( s<10 ) s = '0' + s;
					this.Seconds = s;
				},1000)
			},
			get_day(){
				let week = "星期" + "日一二三四五六".charAt(new Date().getDay());
				let time1 = new Date();
				return time1.getMonth()+1+'/'+time1.getDate()+'&nbsp;&nbsp;'+week;
			},
			
			//定位
			getLocation(){
				let self = this;				
				console.info("getLocation");
				wx.getLocation({
				    type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
				    success: function (res) {
				        let latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
				        let longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
						let ggPoint = new BMap.Point(latitude,longitude);
						let convertor = new BMap.Convertor();
				        let pointArr = [];
				        pointArr.push(ggPoint);		
				        //gcj02-->bd09
				        convertor.translate(pointArr, 3, 5, function(data){
				        	var pointLat,pointLng = 0;
				        	//暂时支持单个点
				        	if(data.status == 0 && data.points.length==1){
				        		pointLat = data.points[0].lat;				        		
				        		pointLng = data.points[0].lng;	
				        		//提交数据,注意转换后经纬度是反的
				        		self.addressInfo.latitude = data.points[0].lng;
				        		self.addressInfo.longitude = data.points[0].lat;
				        	}				      
				        	//转换后的bd09解析成地址	
				        	console.info("pointLat:" + pointLat +"," + pointLng);
				        	var param_point = pointLng + "," + pointLat;
				        	//目标地址
				        	var apoint = new BMap.Point(self.comPoint.lng,self.comPoint.lat);
				        	//现在地址
				        	var bpoint = new BMap.Point(pointLng,pointLat);
				        	
				        	var map = new BMap.Map();
							var distanceMeter = map.getDistance(apoint,bpoint);
							self.addressInfo.distance = Math.round(distanceMeter);
				        	
				        	
				        	//坐标转地址
				        	var url = "http://api.map.baidu.com/geocoder/v2/";
							self.$jsonp({
								url:url,
								params:{
									location:param_point,
									output:'json',
									ak:'vxbA01b31wU4goMSUnYUmIuZV1wi1izk',
									coordtype:'gcj02ll'
								}
							}).then(response => {	
								console.info(response.data.result.formatted_address);
								self.addressInfo.address = response.data.result.formatted_address;
							}).catch(function(error) {
								console.log(error);
							});
					    });
					}
				});															
			},
			
			getTodaySignInfo(){
				var nowDate = new Date().Format("yyyy-MM-dd hh:mm:ss");
				var url = _baseUrl + "/mobile/oa/getSignHistory.do";
				this.$ajax({
						url: url,
						method: 'post',
						params: {
							date: nowDate
						}
					}).then(response => {
						var rtn = response.data;
						if(rtn.success) {
							this.handSignData(rtn.data);
							//console.info("todaySignInfo:"  + JSON.stringify(rtn.data));
						}
					})
					.catch(error => {
						console.log(error);
					});
			},
			handSignData(dataList){
				
				if(dataList.length==0)return;
				for(let i = 0; i < dataList.length; i++){
					//签到
					if(dataList[i].inOffFlag=='1'){
						this.signInData = dataList[i];
					}else{//签退
						this.signOutData = dataList[i];
					}
				}
				
				console.info("this.signInData:" + JSON.stringify(this.signInData));
				console.info("this.signOutData:" + JSON.stringify(this.signOutData));
			},
			getSignStatus(tag){
				let status = "";            
				switch(tag){
					case 1:
						status = "正常";
					case 2:	
						status = "迟到";
					case 3:
						status = "早退";
					case 4:
						status = "休息";
					case 5:
						status = "旷工";
					case 6:
						status = "放假";
					default:
						status = "正常";
				}
				
				return status;
			}
			
		},

		mounted() {
			this.addressInfo.signRemark = this.$route.params.signRemark ==undefined?"":this.$route.params.signRemark;			
		},
		created() {
			MP();
			this.getTime();
			this.get_seconds();	
			this.getLocation(); 
			this.getTodaySignInfo();
			
		}
		
	}
	

</script>

<style scoped>
#app>.customBox>section{
	height: calc(100% - 1rem);
	overflow-x: hidden;
	overflow-y: auto;
}
	
.timeBox{
	background: #fff;
	padding: .5rem 0 ;
}

.timeBox .timeContent{
	border: 5px solid #0DAAF6;
	height: 3rem;
	width: 3rem;
	border-radius: 1.5rem;
	margin: 0 auto;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
}

.timeBox .timeContent p span{
	font-size: .3rem;
}

.timeBox time{
	display: flex;
}

.timeBox time span{
	float: left;
	width: 100%;
	text-align: center;
}

.timeBox time span:nth-of-type(1){
	font-size: .6rem;
	color: #333;
	line-height: .6rem;
}

.timeBox time span:nth-of-type(2){
	margin-top: .2rem;
	font-size: .3rem;
}

.address{
	margin: .24rem 0;
	background: #fff;
	padding: .2rem;
}
.address::after,
.SignBox::after,
.SignTitle::after,
.place::after,
.state p::after,
.state>span::after
{
	display: block;
	content: '';
	clear: both;
}

.address span{
	color: #666;
	float: left;
	width: 100%;
	line-height: .4rem;
}

.address span h1{
	font-size: .3rem;
	float: left;
	width: 1.5rem;
	font-size: .28rem;
}

.address span h2{
	font-size: .3rem;
	float: left;
	width: calc(100% - 1.5rem);
}

.SignBox{
	height: auto;
	padding: .2rem 0;
	background: #fff;
	overflow: hidden;
}

.SignBox>div{
	width: 2px;
	height: 2rem;
	float: left;
	margin: 0 .3rem;
	border-radius: 1px;
}

.SignBox>ul{
	float: left;
	width: calc(100% - 2px - .6rem);
}

.SignBox .dl dd:nth-of-type(1){
	
}

.SignBox .SignTitle{
	position: relative;
}

.SignBox .SignTitle span:nth-of-type(1){
	width: 20px;
	height: 20px;
	border-radius: 20px;
	display: block;
	background: rgb(48,182,251);
	position: absolute;
	left: -.5rem;
	top: -.06rem;
	display: flex;
	justify-content: center;
	align-items: center;
}

.SignBox .SignTitle span:nth-of-type(1) h1{
	width: 10px;
	height: 10px;
	border-radius: 10px;
	background: #00E079;
	
}
.SignBox .SignTitle span:nth-of-type(1) .colorAsh{
	background: #fff;
}

.SignBox .SignTitle span:nth-of-type(1) h2{
	width: 2px;
	height: 10rem;
	position: absolute;
	left: 50%;
	margin-left: -1px;
	top: 20px;
	background: #0DAAF6;
}

.SignBox .SignTitle span:nth-of-type(2){
	font-size: .3rem;
	color: #666;
	float: left;
}

.SignBox .SignTitle span:nth-of-type(3){
	font-size: .28rem;
	color: #666;
	float: right;
	margin-right: .3rem;
}

.SignBox .state{
	margin: .06rem 0;
}

.state p{
	
}

.state p a{
	float: left;
	font-size: .3rem;
	color: #666;
	width: calc(100% - 1.88rem);
	text-align: justify;
	
}

.state p span{
	float: left;
	margin:  0 0 0 .4rem;
	font-size: .3rem;
	color: #666;
}

.SignBox .place{
	margin: .06rem 0;
}

.place i{
	float: left;
	width: .4rem;
	text-align: center;
}

.place i:before{
	font-size: .4rem;
	color: #0DAAF6;
}

.place>span{
	color: #666;
	float: left;
	font-size: .3rem;
}

.place>span:nth-of-type(1){
	width: 1.3rem;
}

.place>span:nth-of-type(2){
	width: calc(100% - 1.9rem);
	word-wrap: break-word;
	text-align: justify;
}

.state>span{
	
}

.state>span>h4{
	margin: 0 0 0 .4rem;
	float: left;
	color: #666;
	font-size: .3rem;
}

.SignBox .state>span>span{
	display:inline-block;
	padding: .08rem .2rem;
	background: #ffa631;
	border-radius: .1rem;
	color: #fff;
	float: left;
	/*margin-left: .06rem;*/
}

.SignIn span{
	display:inline-block;
	padding: .2rem;
	margin: .06rem;
	border-radius: .12rem;
	background: #0DAAF6;
	color: #fff;
	font-size: .3rem;
}

	/*
	 * ]
	
	.btn-block {
		border-radius: 5px;
		height: 0.5rem;
	}
	
	button {
		width: 80%;
		height: 0.8rem;
	}
	
	h1 {
		text-align: center;
		color: #1E90FF;
		font-size: .5rem;
	}
	
	h2 {
		text-align: center;
		font-size: .3rem;
		color: #2E8B57;
	}
	
	div.bottom-button {
		display: flex;
	}
	
	div.bottom-button div {
		flex: 1;
		padding-left: 5px;
		padding-right: 5px;
	}
	
	div.bottom-button div button {
		margin-top: 5px !important;
	}
	
	.showBtn {
		background-color: #A9A9A9;
	}
	
	.remarks{
		width: 70%;
		margin: auto;
	}
	
	.remarks p{
		font-size: .3rem;
		color: #666;
		text-align: justify;
	}*/
</style>


