
import Vue from 'vue';
import { Layout } from 'vue-ydui/dist/lib.rem/layout';
import MapView from '@/components/common/Map.vue';
import router from '@/router/index';
import { Icons } from 'vue-ydui/dist/lib.rem/icons';
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'

import {MP} from '../../../static/js/map';
import wx from 'weixin-js-sdk';

Vue.component(Icons.name, Icons);
Vue.component(Flow.name, Flow);
Vue.component(FlowState.name, FlowState);
Vue.component(FlowLine.name, FlowLine);
export default {
data() {
return {
height: 7,
addressInfo:{
longitude: 0.0,
latitude: 0.0,
address:"定位中..",
distance:0,
signRemark:""
},
isShowIn: "",
isShowOut: "",
showLocateBtn: 'none',
sysVer:"",
changedPoint:{},
time: '00:00',
Seconds:'00',
comPoint:{//对比坐标
lat:113.369199,
lng:23.096371
},
signInData:{},
signOutData:{},
userInfo:{
type:''
}

}
},
components: {
Flow,
FlowState,
FlowLine
},
computed: {
isShowInBtn: function() {
return this.isShowIn == true ? true : false;
},
isShowOutBtn: function() {
return this.isShowOut == true ? '' : 'none';
},
isShowBtn: function() {
if(this.isShowOut == true || this.isShowIn == true) {
return 'none'
}
return '';
}

},
components: {
MapView,

},
methods: {

signIn() {
console.info("签到数据："+JSON.stringify(this.addressInfo));
var url = _baseUrl + "/mobile/oa/signIn.do";
this.$ajax({
url: url,
method: 'post',
params: {
addressInfo: this.addressInfo
}
}).then(response => {
var rtn = response.data;
if(rtn.success) {
this.$dialog.toast({
mes: rtn.message,
timeout: 800,
icon: 'success',
});
}

})
.catch(error => {
console.log(error);
});
},

signOut() {
var url = _baseUrl + "/mobile/oa/signOut.do";
this.$ajax({
url: url,
method: 'post',
params: {
addressInfo: this.addressInfo
}
}).then(response => {
var rtn = response.data;
if(rtn.success) {
this.$dialog.toast({
mes: rtn.message,
timeout: 800,
icon: 'success',
});
}
})
.catch(error => {
console.log(error);
});
},

isShowButton() {
var url = _baseUrl + "/mobile/oa/isShowSignButton.do";
this.$ajax.get(url).then(response => {
var rtn = response.data;
this.isShowOut = rtn.data.isShowOut;
this.isShowIn = rtn.data.isShowIn;
console.info("isShowIn:" + this.isShowIn + ",isShowOut:" + this.isShowOut);
}).catch(function(error) {
console.log(error);
});
},
mySignHis() {
router.push({
name: 'signHistory'
});

},
getTime(){
let time,h,min;
time = new Date();
h = time.getHours();
min = time.getMinutes();
if( h<10 ) h = '0'+ h;
if( min<10 ) min = '0' + min;
this.time = h +':'+ min;

setInterval(()=>{
time = new Date();
h = time.getHours();
min = time.getMinutes();
if( h<10 ) h = '0'+ h;
if( min<10 ) min = '0' + min;
this.time = h +':'+ min;
},1000);
},
get_seconds(){
let s;
s = new Date().getSeconds();
if( s<10 ) s = '0' + s;
this.Seconds = s;

setInterval(()=>{
s = new Date().getSeconds();
if( s<10 ) s = '0' + s;
this.Seconds = s;
},1000)
},
get_day(){
let week = "星期" + "日一二三四五六".charAt(new Date().getDay());
let time1 = new Date();
return time1.getMonth()+1+'/'+time1.getDate()+'&nbsp;&nbsp;'+week;
},


getLocation(){
let self = this;
console.info("getLocation");
wx.getLocation({
type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
success: function (res) {
let latitude = res.latitude; 
let longitude = res.longitude; 
let ggPoint = new BMap.Point(latitude,longitude);
let convertor = new BMap.Convertor();
let pointArr = [];
pointArr.push(ggPoint);

convertor.translate(pointArr, 3, 5, function(data){
var pointLat,pointLng = 0;

if(data.status == 0 && data.points.length==1){
pointLat = data.points[0].lat;
pointLng = data.points[0].lng;

self.addressInfo.latitude = data.points[0].lng;
self.addressInfo.longitude = data.points[0].lat;
}

console.info("pointLat:" + pointLat +"," + pointLng);
var param_point = pointLng + "," + pointLat;

var apoint = new BMap.Point(self.comPoint.lng,self.comPoint.lat);

var bpoint = new BMap.Point(pointLng,pointLat);

var map = new BMap.Map();
var distanceMeter = map.getDistance(apoint,bpoint);
self.addressInfo.distance = Math.round(distanceMeter);



var url = "http://api.map.baidu.com/geocoder/v2/";
self.$jsonp({
url:url,
params:{
location:param_point,
output:'json',
ak:'vxbA01b31wU4goMSUnYUmIuZV1wi1izk',
coordtype:'gcj02ll'
}
}).then(response => {
console.info(response.data.result.formatted_address);
self.addressInfo.address = response.data.result.formatted_address;
}).catch(function(error) {
console.log(error);
});
});
}
});
},

getTodaySignInfo(){
var nowDate = new Date().Format("yyyy-MM-dd hh:mm:ss");
var url = _baseUrl + "/mobile/oa/getSignHistory.do";
this.$ajax({
url: url,
method: 'post',
params: {
date: nowDate
}
}).then(response => {
var rtn = response.data;
if(rtn.success) {
this.handSignData(rtn.data);

}
})
.catch(error => {
console.log(error);
});
},
handSignData(dataList){

if(dataList.length==0)return;
for(let i = 0; i < dataList.length; i++){

if(dataList[i].inOffFlag=='1'){
this.signInData = dataList[i];
}else{//签退
this.signOutData = dataList[i];
}
}

console.info("this.signInData:" + JSON.stringify(this.signInData));
console.info("this.signOutData:" + JSON.stringify(this.signOutData));
},
getSignStatus(tag){
let status = "";
switch(tag){
case 1:
status = "正常";
case 2:
status = "迟到";
case 3:
status = "早退";
case 4:
status = "休息";
case 5:
status = "旷工";
case 6:
status = "放假";
default:
status = "正常";
}

return status;
}

},

mounted() {
this.addressInfo.signRemark = this.$route.params.signRemark ==undefined?"":this.$route.params.signRemark;
},
created() {
MP();
this.getTime();
this.get_seconds();
this.getLocation();
this.getTodaySignInfo();

}

}





